* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  width: 100%;
}

.app {
  max-width:  80%;
  margin: 0 auto;
}

section {
  padding: 15px 10px;
}





.header-content {
    min-width: 70px;
    min-height: 48px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    padding: 10px 0;
}
header .logo a {
    text-decoration: none;
    text-transform: uppercase;
    color: black;
}
header nav {
    display: flex;
}
nav ul li {
     list-style: none;
     display: inline-block;
}
nav ul li a {
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 15px;
}
nav ul li a:hover {
    color: lightseagreen;
}
nav .nav-cart {
    cursor: pointer;
    position: relative;
}
nav .nav-cart span {
    position: absolute;
    top: -12px;
    right: -7px;
    background-color: red;
    font-size: 10px;
    color: white;
    padding: 3px 5px ;
    border-radius: 70%;
    z-index: -1;
}
header .menu,
header .close {
    cursor: pointer;
    display: none;
}
@media (max-width: 650px) {
    header ul{
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        background-color: white;
        z-index: 99;
        opacity: 0.97;
        transition: 0.3s linear;
    }
    header .menu,
    header .close {
        display: block;
    }
    header .close{
        position: absolute;
        right: 60px;
        top: 10px;
    }
    header ul.toggle {
        top: 0;
    }
}
#product {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 50px;
}
.card {
    text-align: center;
    min-width: 300px;
    min-height: 300px;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: 2px 8px 20px #ddd;
    margin: 10px;
    transition: 0.3s linear;
}
.card:hover {
    box-shadow: none;
}
.card img {
    max-width: 300px;
    width: 100%;
    height: 100%;
    max-height: 390px;
    display: block;
    object-fit: contain;
}
.content {
    margin: 15px 0 0 ;
}
.card h3 a {
    text-decoration: none;
    color: #333;
}
.card h3 a:hover {
    color: crimson;
}
.card span {
    display: block;
    color:  rgb(14, 168, 14);
    margin: 15px 0;
}
.card p {
    max-width: 250px;
    margin: 0 auto;
    margin-top: 5px;
}
.card button {
    border: none;
    outline: none;
    background-color: #333;
    color: whitesmoke;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.card button:hover {
    color: lightseagreen;
}


.details {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 50px 0; 
}
.details img {
    max-width: 450px;
    min-width: 290px;
    width: 100%;
    height: 480px;
    display: block;
    object-fit: cover;
}
.details .box{
    max-width: 500px;
    min-width: 290px;
    height: 250px;
    margin: 25px;
}
.box .row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}
.box .row h2 {
    text-transform: uppercase;
    letter-spacing: 2px;
}
.box .row span {
    color:  rgb(14, 168, 14);
}
.box .colors button {
    width: 30px;
    height: 30px;
    border: none;
    outline: none;
    margin-right: 5px;
    cursor: pointer;
    box-shadow: 2px 8px 20px #ddd;
}
.box p {
    line-height: 1.5;
    margin: 15px 0;
    background-color: #ffffffff;
}
.thumb {
    max-width: 600px;
    height: 290px;
    display: flex;
    cursor: pointer;
    overflow-y: scroll;
    height: 45vh;
    padding: 0;
}
.thumb::-webkit-scrollbar {
    height: 10px;
}
.thumb::-webkit-scrollbar-thumb {
    background-color: lightseagreen;
    border: 3px solid var(--black-color);
    border-radius: 5px;
}
.scroll-button {
    display: block;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    background-color: #333;
}
.thumb img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
    box-shadow: 2px 8px 20px #ddd;
    margin-right: 5px;
    opacity: 0.7;
    border-radius: 5px;
}
.thumb img.active {
    opacity: 1;
    border: 1px solid lightseagreen;
}
.box .cart {
    border: none;
    background-color: #333;
    outline: none;
    color: white;
    font-size: 15px;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 10px 25px;
    margin-top: 20px;
    display: inline-block;
}
.box .cart:hover {  
    color: lightseagreen; 
}


@media  (max-width: 414px) { 
    .details img {
        min-width: 150px;
        height: 390px;
    }
}

section .cart {
    position: relative;
    border: 1px solid #ccc;

}
.cart img {

    max-height: 316px;
    object-fit: contain;
}
.amount span {
    color:  rgb(14, 168, 14);
    padding:  0 20px;
}
.amount .count {
    width: 40px;
    height: 40px;
    border: 1px solid #555;
    outline: none; 
    background-color: transparent;
    cursor: pointer;
}
.delete {
    position: absolute;
    top: 5px;
    right: 10px;
    color: rgb(58, 52, 52);
    cursor: pointer;
    font-weight: 600;
}
.total {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.total a {
    text-decoration: none;
    background-color: #333;
    color: whitesmoke;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 10px 25px;
}
.total a:hover {
    color: lightseagreen;
} 
.total h3 {
    margin-right: 50px;
    color: rgb(14, 168, 14);
}
.checkout-button {
    text-decoration: none;
    background-color: #333;
    color: whitesmoke;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 10px 25px;
}
.checkout-button:hover {
    color: lightseagreen;
}

.payment-result {
    text-align: center;
    display: table; 
    height: 100%;
    width: 100%;
}

.payment__statement {
    display: table-cell;
    vertical-align: middle;
}

section {
    padding: 10px 0 0;
    height: 90vh;
    align-content: center;
}

.about {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.image-container {
    width: 50%;
}

.image-container .image {
    width: auto;
    height: 500px;
    background-image: url('https://images.unsplash.com/photo-1558104631-0fa41a8f6c20?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
    background-position: center;
    background-repeat: no-repeat;
}

.about-content {
    width: 50%;
    margin-left: 120px;
}

.about-content h2{
    font-size: 50px;
    margin-bottom: 20px;
    color: rgb(34, 32, 32);
}

.about-content p {
    line-height: 1.6;
    color: rgb(88, 83, 83);
}

@media only screen and (max-width: 920px) {
    section {
        margin-top: 20px;     
    }

    .about {
        padding: 20px 0;
        margin: 0 auto;
        display: block;
        align-items: center;
    }

    .about h2 {
        font-size: 40px;
    }

    .image-container {
        width: 100%;
        margin-bottom: 20px;
    }
    
    .image-container .image {
        height: 300px;
    }
    
    .about-content {
        width: 100%;
        margin: 0 auto;
    }
    
}

.contact {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form__container {
    width: 50%;
    padding: 20px;
    display: flex;
    align-items: center;
    height: 100%;
}

.form__container form {
    margin: 0 auto;
    padding: 30px;
    width: 100%;
    font-size: 20px;
    color: rgb(31, 29, 29);
}

.form__button {
    width: 100%;
    padding: 10px;
    margin-top: 60px;
    font-size: inherit;
    background-color: #333;
    color: whitesmoke;
    cursor: pointer;
    border: none;
    transition: all .3s ease-out;
}

.form__button:hover {
    color: lightseagreen;  
    box-shadow: -14px 18px 32px -18px rgba(0,0,0,0.73);
    -webkit-box-shadow: -14px 18px 32px -18px rgba(0,0,0,0.73);
    -moz-box-shadow: -14px 18px 32px -18px rgba(0,0,0,0.73);
}

.form__input input {
    padding: 10px 0;
    font-size: inherit;
}

.form__input {
    margin-bottom: 20px;
}

.form__input textarea {
    color: inherit;
    resize: none;
}

.form__input label {
    display: block;
    padding: 10px 0;
}

.border-width {
    border: none;
    border-bottom: 2px solid rgb(37, 37, 37);
    width: 100%;
}

.border-width:focus { 
    outline: none; 
    caret-color: rgb(0, 140, 255);
}

.about-contact {
    width: 50%;
    margin-left: 120px;
}

.about-contact h2 {
    font-size: 50px;
    margin-bottom: 20px;
    color: rgb(34, 32, 32);
}

.about-contact p {
    line-height: 1.6;
    color: rgb(88, 83, 83);
}



