
.header-content {
    min-width: 70px;
    min-height: 48px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    padding: 10px 0;
}
header .logo a {
    text-decoration: none;
    text-transform: uppercase;
    color: black;
}
header nav {
    display: flex;
}
nav ul li {
     list-style: none;
     display: inline-block;
}
nav ul li a {
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 15px;
}
nav ul li a:hover {
    color: lightseagreen;
}
nav .nav-cart {
    cursor: pointer;
    position: relative;
}
nav .nav-cart span {
    position: absolute;
    top: -12px;
    right: -7px;
    background-color: red;
    font-size: 10px;
    color: white;
    padding: 3px 5px ;
    border-radius: 70%;
    z-index: -1;
}
header .menu,
header .close {
    cursor: pointer;
    display: none;
}
@media (max-width: 650px) {
    header ul{
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        background-color: white;
        z-index: 99;
        opacity: 0.97;
        transition: 0.3s linear;
    }
    header .menu,
    header .close {
        display: block;
    }
    header .close{
        position: absolute;
        right: 60px;
        top: 10px;
    }
    header ul.toggle {
        top: 0;
    }
}